@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: "Do Hyeon";
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

Link {
  -webkit-text-decoration: 'none';
          text-decoration: 'none';
}

header {  
    height: 60px;
    padding: 1rem;
    color: white;
    background: #FFE32C;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
}
.gray_button {
    border-radius: 38px;
    font-size: 45px;
    color: white;
    display: inline-block;
    width: '204px';
    background: #999A9C 0% 0% no-repeat padding-box;
    margin: 20px;
    padding: 12px;
}

.yellow_button {
    border-radius: 38px;
    font-size: 45px;
    color: white;
    display: inline-block;
    width: '204px';
    background: #FFDD00 0% 0% no-repeat padding-box;
    margin: 20px;
    padding: 12px;
}
.ImageCenter {
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ImagePreview {
    opacity: 0;
    display: block;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: absolute;
    overflow: hidden;
    transition: all 500ms ease-in-out 250ms;
}
.ImagePreview.Show {
    opacity: 1;
}

.ImageProgress {
    opacity: 0;
    display: block;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    overflow: hidden;
    transition: all 500ms ease-in-out 250ms;
}
.ImageProgress.Show {
    opacity: 1;
}

.ImagePreview > div {
    position: absolute;
    background-size: cover;
    -webkit-filter: blur(20px);
            filter: blur(20px);
    left: -40px;
    right: -40px;
    bottom: -40px;
    top: -40px;
}

.DropArea {
    background: #efefef;
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(60vw - 60px);
    height: calc(50vh - 50px);
    border: solid 40px transparent;
    transition: all 250ms ease-in-out 0s;
    position: relative;
}

.DropArea.Over {
    border: solid 40px rgba(0, 0, 0, 0.2);
}

.ImageProgress {
    display: block;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    overflow: hidden;
}
.ImageProgress > .ImageProgressImage {
    opacity: 0.3;
    position: absolute;
    background-position: center center;
    background-size: cover;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.ImageProgress > .ImageProgressUploaded {
    position: absolute;
    background-position: center center;
    background-size: cover;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-clip-path: inset(0% 0 0 0);
            clip-path: inset(0% 0 0 0);
}

.Status {
    background: rgba(0, 0, 0, 0.3);
    display: block;
    color: #939393;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    line-height: calc(50vh - 50px);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transition: all 250ms ease-in-out 0s;
}

.Status {
    background: transparent;
    display: block;
    color: #939393;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    line-height: calc(50vh - 50px);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transition: all 250ms ease-in-out 0s;
}

.Status.Uploading {
    background: rgba(0, 0, 0, 0.3);
    color: white;
}
